body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 3rem 5%;
  font-size: 100%;
  position: relative;
  background-color: hsl(226, 43%, 10%);
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1.1;
}

.attribution {
  font-size: 11px;
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
  color: hsl(246, 80%, 60%);
}

.attribution a {
  color: hsl(246, 80%, 60%);
}

.App-header {
  /* display: none; */
  width: 100%;
  border-radius: 0.9rem;
  background-color: hsl(235, 46%, 20%);
  margin-bottom: 1.3rem;
}

.App-header .row1 {
  width: 100%;
  background-color: hsl(246, 80%, 60%);
  border-radius: inherit;
  padding: 1.8rem 8%;
  display: flex;
  gap: 5%;
}

.App-header .row1 img {
  width: 3.6rem;
  height: 3.6rem;
  border: 3px solid white;
  border-radius: 50%;
}

.row1__col2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}

.row1__col2 h1 {
  font-size: 1rem;
  font-weight: 300;
}

.row1__col2 h2 {
  font-size: 1.5rem;
  font-weight: 400;
  
}

.App-header .row2 {
  display: flex;
  justify-content: center;
  /* gap: 14%; */
  /* padding-block: 1.1rem; */
  position: relative;
}

.App-header .row2 button {
  border: none;
  background-color: inherit;
  color: rgba(255, 255, 255, 0.473);
  font-size: 1.05rem;
  font-weight: 100;
  width: 33.33%;
  padding-block: 6%;
  cursor: pointer;
}

.App-header .row2 button:hover {
  color: rgb(255, 255, 255);
}

.App-header .row2 input[type="radio"] {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  visibility: hidden;
}

.App-header .row2 input[type="radio"]:checked + button {
  font-weight: 300;
  color: rgb(255, 255, 255);
}

.App-header .row2 input[type="radio"]:nth-child(1) {
  left: 0;
}

.App-header .row2 input[type="radio"]:nth-child(3) {
  left: 50%;
  transform: translate(-50%, -50%);
}

.App-header .row2 input[type="radio"]:nth-child(5) {
  right: 0;
}

@media (min-width: 48rem) and (max-width: 63.9rem) {
  main {
    display: flex;
    flex-flow: row wrap;
    gap: 5%;
    row-gap: 1rem;
  }

  .App-header {
    margin-bottom: 2rem;
  }

  .card {
    flex-basis: 47.5%;
  }
}

@media (min-width: 64rem) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;  
    padding: 0;
  }

  #root {
    width: 100%;
  }

  .App {
    display: flex;
    gap: 2.5%;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-inline: 5%;
    /* min-height: 100vh; */
  }

  .App-header {
    margin: 0;
    width: 21%;
    height: 100%;
    min-height: 65vh;
    /* border-radius: 0.8rem; */
  }

  .App-header .row1 {
    display: flex;
    flex-direction: column;
    gap: clamp(1.5rem, 3.44vw, 2.2rem);
    padding: clamp(1rem, 2.8vw, 1.8rem) 12% clamp(2rem, 7vw, 4.5rem) 12%;
    /* border-radius: 0.8rem; */
  }

  .App-header .row1 img {
    height: clamp(3rem, 8.9vw, 5.7rem);
    width: clamp(3rem, 8.9vw, 5.7rem);
  }

  .App-header .row1 .row1__col2 {
    
  }

  .App-header .row1 .row1__col2 h1 {
    font-weight: 100;
  }

  .App-header .row1 .row1__col2 h2 {
    font-size: clamp(1.7rem, 4.2vw, 2.7rem);
    line-height: 1.2;
    font-weight: 100;
  }

  .App-header .row2 {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: clamp(1rem, 1.72vw, 1.1rem) 10% clamp(1rem, 2vw, 1.3rem) 10%;
  }

  .App-header .row2 button {
    text-align: left;
    width: 100%;
    padding-block: 5%;
  }

  main {
    display: flex;
    flex-flow: row wrap;
    width: 64%;
    height: 100%;
    min-height: 65vh;
    gap: 4%;
    row-gap: clamp(1.5rem, 2vw, 2rem);
  }

  .card {
    flex-basis: 30%;
    margin: 0;
    display: flex;
    align-items: flex-end;
    /* padding: ; */
    /* padding-top: 6.7rem; */
    /* display: none; */
  }

  .card .main {
    padding: clamp(1.5rem, 3vw, 2rem) 12% clamp(1.5rem, 3vw, 2rem) 12%;
    /* margin-top: 3rem; */
  }

  .card .card__row1 {
    margin-bottom: clamp(1.4rem, 3.75vw, 2.4rem);
  }

  .card .card__row1 p {
    font-size: 1.1rem;
  }

  .card .card__row2 {
    flex-direction: column;
    align-items: flex-start;
  }

  .card__row2 p {
    
  }

  .card__row2 .current-hours {
    font-size: clamp(2rem, 5.2vw, 3.3rem);
    margin-bottom: 1.1rem;
  }

  .card__row2 .previous-hours {
    font-size: 0.9rem;
  }
}