.card {
    width: 100%;
    border: none;
    border-radius: 1rem;
    overflow: hidden;
    background-color: rgba(255, 153, 0, 0.788);
    position: relative;
    margin-bottom: 1.1rem;
}

.card:nth-child(1) {
    background-color: hsla(15, 100%, 70%, 0.7);
}

.card:nth-child(2) {
    background-color: hsla(195, 74%, 62%, 0.7);
}

.card:nth-child(3) {
    background-color: hsla(348, 100%, 68%, 0.7);
}

.card:nth-child(4) {
    background-color: hsla(145, 58%, 55%, 0.7);
}

.card:nth-child(5) {
    background-color: hsla(264, 64%, 52%, 0.7);
}

.card:nth-child(6) {
    background-color: hsla(43, 84%, 65%, 0.7);
}

.card img {
    position: absolute;
    top: -0.7rem;
    right: 5%;
    z-index: -2;
}

.card .main {
    background-color: hsl(235, 46%, 20%);
    color: white;
    width: 100%;
    padding: 1.3rem 7% 1.3rem 7%;
    border-radius: 0.9rem;
    margin-top: 2rem;
    z-index: 1;
    cursor: pointer;
}

.card .main:hover {
    filter: brightness(1.2);
}

.card .card__row1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.6rem;
    height: auto;
}

.card .card__row1 p {
    font-size: 1rem;
    font-weight: 400;
}

.card .card__row1 input {
    height: 5px;
}

.card .card__row1 input:hover {
    filter: brightness(20);
}

.card .card__row2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.current-hours {
    font-size: 2rem;
    line-height: 0.9;
}

.previous-hours {
    color: rgba(255, 255, 255, 0.548);
}